pre {
  overflow-x: hidden;
  padding: 15px 15px 10px 15px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 10px;
  font-size: 13px;
  font-family: $font-family-mono;
  line-height: 22px;
  position: relative;
  max-height: 800px;

  .code-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    border-radius: 0 2px;
    padding: 0;
    font-family: "JetBrains Mono", monospace;
    font-weight: 800;
    font-size: 0.9em;
    line-height: 1.7;
    color: #fff;
    background-color: #8c8c8c;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0em;
  }

  .code-copy-btn:hover {
    background-color: #666;
    color: #2bbc8a;
    
  }

  code {
    display: block;
    padding: 0;
    border: none;
  }
}

code {
  font-family: $font-family-mono;
  padding: 1px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
}



.highlight {

  & > div {
    border-radius: 2px;
    -webkit-border-radius: 2px;
  }

  pre {
    border: none;
    background: none;
  }

  table {

    pre {
      margin-top: 0;
    }

    td:first-child {
      pre {
        padding-right: 0;
      }
    }

    td:last-child {
      pre {
        padding-left: 0;
      }
    }
  }
}

/* breadcrumb */
.breadcrumb {background: #fafafa;padding:4px 15px; margin-bottom:40px; list-style:none;border-radius: 5px; }
.breadcrumb>li{display:inline-block;opacity:.7;}
.breadcrumb>li+li:before {padding:0 5px; color: #ccc; content: ">"; }
.breadcrumb li a{text-decoration:none;}
.dark-theme .breadcrumb {background: #252627;}


